.header-footer {
  .footer {
    width: 100%;
    box-shadow: 2px 1px 10px #00000029;
    align-items: center;

    .footer-wrapper {
      display: flex;
      width: 100%;
      justify-content: space-between;
      align-items: center;

      .footer-content {
        display: flex;
        padding: 30px 0 30px 0;
        justify-content: space-between;
        // gap: 14px;
        width: 65%;

        .footer-text {
          text-align: left;
          font: normal normal normal 14px/20px Poppins;
          letter-spacing: 0px;
          color: #000000;
          opacity: 1;
        }
      }

      .follow {
        display: flex;
        text-align: left;
        width: 35%;
        justify-content: flex-end;
        font: normal normal normal 14px/20px Poppins;
        letter-spacing: 0px;
        color: #000000;
        opacity: 1;

        .sosmed {
          padding: 0 10px 0 10px;
        }
      }
    }
  }

  .under-footer {
    display: flex;
    padding: 20px 0 20px 7%;
    max-width: $max-width;
    min-width: $min-width;
    text-align: left;
    font: normal normal normal 12px/16px Poppins;
    letter-spacing: 0.6px;
    color: #707070;
    opacity: 1;
    gap: 5px;
  }
}