.recommend-produk {

  &-header {
    display: flex;
    margin-bottom: 20px;
    justify-content: space-between;

    .header-text-left {
      text-align: left;
      font: normal normal bold 16px/20px Poppins;
      letter-spacing: 0px;
      color: #000000;
      opacity: 1;
      width: 70%;
    }

    .header-text-right {
      text-align: left;
      font: normal normal bold 14px/16px Poppins;
      letter-spacing: 0px;
      color: #026BB9;
      opacity: 1;
      // width: 100%;
    }
  }

  &-card {
    display: flex;
    // width: 100%;
    margin-bottom: 20px;
    justify-content: space-between;
    align-items: center;

    .recommend-produk-img {
      background: transparent 0% 0% no-repeat padding-box;
      box-shadow: 2px 1px 10px #00000029;
      border-radius: 5px;
      opacity: 1;
      height: 104px;
      width: 120px;
    }

    .information-produk {
      .title-produk {
        text-align: left;
        font: normal normal normal 14px/16px Poppins;
        letter-spacing: 0px;
        color: #000000;
        opacity: 1;
        margin-bottom: 5px;
      }

      .flexing-text {
        .star {
          display: flex;
          align-items: center;

          .terjual {
            margin-left: 10px;
            width: fit-content;
            display: flex;

            &-blue {
              text-align: left;
              font: normal normal normal 14px/16px Poppins;
              letter-spacing: 0px;
              color: #026BB9;
            }

            &-grey {
              text-align: left;
              font: normal normal normal 14px/16px Poppins;
              letter-spacing: 0px;
              color: #707070;
              margin-left: 3px;
            }
          }
        }

        .price {
          display: flex;
          align-items: center;
          margin: 5px 5px 10px 5px;

          .cross-line {
            text-align: left;
            text-decoration: line-through;
            font: normal normal normal 12px/16px Poppins;
            letter-spacing: 0px;
            color: #000000;
            opacity: 1;
          }

          .red-text {
            text-align: left;
            font: normal normal bold 14px/16px Poppins;
            letter-spacing: 0px;
            color: #FF0000;
            margin-left: 10px;
            opacity: 1;
          }
        }

        .box-text {
          display: flex;
          align-items: center;

          .red-box {
            background: #FF0000 0% 0% no-repeat padding-box;
            border-radius: 5px;
            opacity: 1;
            text-align: center;
            font: normal normal bold 12px/16px Poppins;
            letter-spacing: 0px;
            color: #FFFFFF;
            opacity: 1;
            padding: 6px;
            cursor: pointer;
          }

          .grey-box {
            text-align: left;
            font: normal normal bold 12px/16px Poppins;
            letter-spacing: 0px;
            color: #707070;
            opacity: 1;
            background: #F0F0F0 0% 0% no-repeat padding-box;
            border-radius: 5px;
            opacity: 1;
            padding: 6px;
            margin-left: 10px;
            cursor: pointer;
          }
        }
      }
    }
  }
}