.product-details {
  margin-bottom: 40px;

  .back-to-before {
    color: $blue-primary;
    font-size: 14px;
    font-weight: bold;
    cursor: pointer;
    display: inline-block;
    margin-bottom: 20px;
    transition: 0.1s linear;

    &:hover {
      filter: $filter-grey-text;
    }

    span {
      margin-right: 10px;

      img {
        transform: rotate(90deg);
      }
    }
  }

  .details {
    display: grid;
    grid-template-columns: 420px 1fr;
    gap: 30px;

    &-photos {
      position: relative;

      .highlight-photo {
        position: relative;
        display: flex;
        // width: auto;
        // max-width: 420px;
        height: 500px;
        // max-height: 420px;
        background: #005050;
        box-shadow: $box-shadow-value;
        overflow: hidden;

        img {
          position: relative;
          width: 100%;
          // max-width: 420px;
          min-height: 100%;
        }
      }

      .slider-highlight {
        position: relative;
        top: 20px;
        width: auto;
        height: auto;
        padding: 0 5%;

        .slider-photo {
          padding: 0 10%;

          img {
            background: transparent 0% 0% no-repeat padding-box;
            box-shadow: 2px 1px 10px #00000029;
            border-radius: 5px;
            opacity: 1;
            width: 100px;
            height: 100px;
          }
        }

        .slick-current {
          img {
            border: 3px solid $blue-primary;
          }
        }

        .slick-prev {
          width: 50px;
          height: 50px;
          z-index: $z-index-slick-next-prev;
          border-radius: 50%;

          &::before {
            content: url("../../assets/slider-icons/arrow-slider.svg");
          }
        }

        .slick-next {
          top: 30px;
          width: 50px;
          height: 50px;
          z-index: $z-index-slick-next-prev;
          border-radius: 50%;
          transform: scaleX(-1);

          &::before {
            content: url("../../assets/slider-icons/arrow-slider.svg");
            transform: rotate(90deg);
            position: relative;
          }
        }
      }
    }

    &-description {
      &-category {
        font-size: 14px;
        font-weight: bold;
        color: $grey-text;
        margin-bottom: 5px;
      }

      &-title {
        font-size: 18px;
        font-weight: bold;
      }

      &-information {
        display: flex;
        margin-bottom: 10px;

        &>*+* {
          margin-left: 15px;
        }

        span {
          color: $blue-primary;
        }
      }

      &-old-price {
        display: flex;
        align-items: center;
        margin-bottom: 5px;

        .discount-price {
          font-size: 12px;
          color: $grey-text;
          text-decoration: line-through;
          margin-right: 10px;
        }
      }

      .information-title {
        font-size: 14px;
        margin-bottom: 10px;
      }

      &-actual-price {
        font-size: 18px;
        color: $red-accent;
        font-weight: bold;
        margin-bottom: 20px;
      }

      &-store-promo {
        width: 100%;
        margin-bottom: 20px;

        .voucher-wrapper {
          display: grid;
          grid-template-columns: 1fr 1fr;
          gap: 10px;
          width: 100%;
        }
      }

      &-product-quality {
        margin-bottom: 20px;

        .quality-wrapper {
          &>*+* {
            margin-left: 10px;
          }
        }
      }

      &-sizes {
        .information-title {
          display: flex;
          justify-content: space-between;

          span {
            color: $blue-primary;
            cursor: pointer;
            transition: 0.1s linear;

            &:hover {
              filter: $filter-grey-text;
            }
          }
        }

        margin-bottom: 20px;
        width: 83%;
        position: relative;

        @import "../sizes/size-chart";
      }

      &-shipping {
        .shipping-promo {
          font-size: 14px;
          color: $grey-text;
          margin-bottom: 10px;
        }

        .shipping-counter {
          display: flex;
          gap: 10px;
          width: 100%;

          @import "./shipping-dropdown";
        }
      }
    }
  }
}