.breadcrumb {

  .navigation-tab {
    background: #F0F0F0 0% 0% no-repeat padding-box;
    border-radius: 12px;
    opacity: 1;
    display: flex;
    padding: 15px 30px;
    align-items: center;
    margin-bottom: 30px;

    &-arrow {
      color: #707070;
      margin-left: 10px;
      margin-right: 10px;
      opacity: 1;
      padding-top: 2px;
    }

    &-inactive {
      text-align: left;
      font: normal normal bold 14px/16px Poppins;
      letter-spacing: 0px;
      color: #026BB9;
      opacity: 1;
      cursor: pointer;

    }

    &-active {
      text-align: left;
      font: normal normal bold 14px/16px Poppins;
      letter-spacing: 0px;
      color: #707070;
      opacity: 1;
    }
  }
}