.product-info-bottom {
  position: fixed;
  bottom: 0;
  background-color: white;
  height: 100px;
  width: 100%;
  box-shadow: $box-shadow-value;
  font: normal normal bold 14px/16px Poppins;

  .container {
    padding: 0 7%;
    height: 100%;
    margin: auto inherit;
    display: flex;
    justify-content: space-between;
    flex-direction: row;

    .left {
      display: flex;
      flex-direction: row;
      justify-content: left;
      height: 100%;

      .avatar {
        display: flex;
        flex-direction: column;
        justify-content: center;

        .avatar-img {
          border-radius: 50%;
          max-width: 50px;
          max-height: 50px;
        }
      }

      &-text {
        display: flex;
        flex-direction: column;
        margin-left: 20px;
        justify-content: center;

        &-shop {
          margin-bottom: 10px;
        }

        &-button {
          display: flex;
          flex-direction: row;

          &-detail,
          &-chat {
            cursor: pointer;
            padding: 4px 9px;
            border-radius: 5px;
            border: none;
            font-weight: bold;
            white-space: nowrap;
            text-align: center;
            line-height: 20px;
          }

          &-detail {
            margin-right: 10px;
            background-color: $blue-primary;
            color: #fff;
            &:hover {
              transition: 0.1s;
              filter: brightness(80%);
            }
          }

          &-chat {
            border: $blue-primary solid 1px;
            background-color: #fff;
            color: $blue-primary;

            &:hover {
              transition: 0.1s;
              background-color: $blue-primary;
              color: #fff;
            }
          }
        }
      }
    }

    // ==============================

    .right {
      display: flex;
      flex-direction: flex-end;
      justify-content: center;
      // width: 45%;

      &-price {
        display: flex;
        flex-direction: column;
        justify-content: center;
        margin-right: 30px;
        width: 150px;
        max-width: 150px;

        &-title {
          font-size: 14px;
        }

        &-number {
          margin-top: 5px;
          color: $red-accent;
          font-size: 18px;
        }
      }

      &-quantity {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
        width: 130px;
        max-width: 130px;

        &-cremental {
          cursor: pointer;
          height: 35px;
          width: 35px;
          max-width: 35px;
          background-color: #fff;
          color: $blue-primary;
          border-radius: 5px;
          border: $blue-primary solid 1px;
          font-size: 18px;
          font-weight: bold;

          &:hover {
            transition: 0.1s;
            background-color: $blue-primary;
            color: #fff;
          }

          &.disabled {
            cursor: not-allowed;
            border: 1px solid #999999;
            background-color: #fff;
            color: #666666;
          }
        }

        &-info {
          display: flex;
          font-size: 18px;
          font-weight: bold;
          align-items: center;
        }
      }

      &-button {
        display: flex;
        flex-direction: flex-end;
        text-align: center;
        align-items: center;
        font-size: 18px;
        margin-left: 30px;

        &-buy,
        &-cart {
          cursor: pointer;
          padding: 7px 19px 6px;
          border-radius: 5px;
          border: solid 1px $blue-primary;
          font-weight: bold;
          text-align: center;
          white-space: nowrap;
        }

        &-buy {
          background-color: $blue-primary;
          color: #fff;
          margin-right: 10px;
          &:hover {
            transition: 0.1s;
            filter: brightness(80%);
          }
        }

        &-cart {
          border: $blue-primary solid 1px;
          background-color: #fff;
          color: $blue-primary;

          &:hover {
            transition: 0.1s;
            background-color: $blue-primary;
            color: #fff;
          }
        }

        &-opt {
          height: 35px;
          width: 35px;
          cursor: pointer;
          font-weight: bold;
          border-radius: 5px;
          border: none;
          background-color: #fff;
          color: $blue-primary;
          margin-left: 10px;

          &:hover {
            transition: 0.1s;
            background-color: $blue-primary;
          }

          &:hover .right-button-opt-icon {
            filter: brightness(0) invert(1);
          }

          &-icon {
            height: 35px;
            width: 35px;

            .love {
              background-color: #de1b85;
            }
          }
        }
      }
    }
  }
}
