.shipping-dropdown {
  width: 100%;
  box-shadow: $box-shadow-value;
  padding: 8px 10px;
  font-weight: bold;
  color: $grey-text;
  font-size: 14px;
  border-radius: 5px;
  display: flex;
  justify-content: space-between;
  cursor: pointer;
  transition: 0.1s linear;

  &:hover {
    filter: $filter-grey-text;
  }
}