.size-chart {
  width: 100%;
  display: grid;
  grid-template-columns: repeat(auto-fill, 36px);
  gap: 10px;

  .size {
    border: 1px solid $blue-primary;
    border-radius: 5px;
    color: $blue-primary;
    padding: 0 8px;
    line-height: 36px;
    font-size: 14px;
    width: 36px;
    height: 36px;
    cursor: pointer;
    transition: 0.1s linear;

    &:hover,
    &:focus,
    &:active {
      background: $blue-primary;
      color: #fff;
    }

    &.empty {
      border: none;
      background: url('../../assets/detail-page-icons/cross.svg');
      background-position: center;
      cursor: default;
    }
  }
}