.big-cart {
  margin-bottom: 52px;

  .big-cart-wrapper {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-between;
    // gap: 100px;

    &.rtl {
      flex-direction: row-reverse;
    }

    .img-wrapper {
      box-shadow: $box-shadow-value;
      width: 60%;
      max-width: 757px;
      height: 60%;
      display: flex;
      justify-content: center;
      align-items: center;

      .big-cart-image {
        width: 100%;
        height: auto;
      }
    }

    .text-wrapper {
      width: 320px;
      // margin-right: 8%;
      margin: 80px;
      // @media (max-width:1192px) {
      //   margin-right: 20px;
      // }

      .header-text {
        text-align: left;
        font: normal normal bold 16px/16px Poppins;
        letter-spacing: 0px;
        color: #000000;
        opacity: 1;
      }

      .child-text {
        text-align: left;
        margin: 10px 0 10px 0;
        font: normal normal normal 14px/20px Poppins;
        letter-spacing: 0px;
        color: #707070;
        opacity: 1;
        margin-block: 10px;
      }

      .box-text {
        font: normal normal bold 14px/16px Poppins;
        color: $blue-primary;
        padding: 8px 20px;
        // width: fit-content;
        text-align: center;
        background: #ffffff 0% 0% no-repeat padding-box;
        border: 2px solid $blue-primary;
        border-radius: 8px;
        transition: 0.1s linear;
        cursor: pointer;

        &:hover {
          background: $blue-primary;
          color: #fff;
        }
      }
    }

    .text-wrapper-right {
      width: 280px;
      margin-left: 8%;
    }

    .text-wrapper-left,
    .text-wrapper-right {
