.pagination {
  a {
    color: #000;
    float: left;
    padding: 8px 16px;
    text-decoration: none;
    font-weight: bold;
    transition: 0.1s all linear;

    &:hover {
      color: $blue-primary;
    }
  }

  .prev {
    color: $grey-text;
    float: left;
    padding: 8px 16px;
    text-decoration: none;
    transition: background-color .3s;
    border: 1px solid $grey-bg;
    border-radius: 5px;
    transition: 0.1s all linear;

    &:hover {
      color: $blue-primary;
      border: 1px solid $blue-primary;
    }
  }

  .next {
    color: $grey-text;
    float: left;
    padding: 8px 16px;
    text-decoration: none;
    transition: background-color .3s;
    border: 1px solid $grey-bg;
    border-radius: 5px;
    transition: 0.1s all linear;

    &:hover {
      color: $blue-primary;
      border: 1px solid $blue-primary;
    }
  }
}