.cart {
  display: flex;
  // width: 20px;
  // height: 27px;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  transition: 0.1s all linear;
  color: $grey-text;
  font-size: 1.5em;
  // transform: scale(0.8);
  margin-right: 30px;
  margin-right: clamp(15px, calc(2vw + 2px), 30px);

  &:hover {
    filter: brightness(60%);
  }
}