.navbar-wrapper {
  background: #fff;
  // position: relative;
  z-index: $z-index-dd-menus;

  .navbar {
    height: 95px;
    width: 100%;
    position: relative;
    display: flex;
    align-items: center;
    font-size: 14px;
    background: #fff;
    padding-top: 15px;
    justify-content: center;

    &-left {
      // width: 134px;
      position: absolute;
      left: 0;

      .logo {
        font-size: 18px;

        a {
          color: $blue-primary;
        }
      }
    }

    &-center {
      position: absolute;
      left: 50%;
      transform: translateX(-50%);
      display: flex;
      align-items: center;
      justify-content: center;
      flex: 1;
      // padding: 0 clamp(20px, 4vw, 72px);

      // justify-content: center;
      // align-items: center;

      @import './dropdown-category';
      @import './form-navbar';
    }

    &-right {
      position: absolute;
      right: 0;
      display: flex;
      align-items: center;
      // justify-content: space-between;
      // * {
      //   margin-left: 30px;

      //   * {
      //     margin-left: 0;
      //   }
      // }

      @import './cart';
      @import './like';

      .login {
        padding: 7px 20px;
        background: $blue-primary;
        border-radius: 12px;
        color: #fff;
        font-weight: bold;
        transition: 0.1s all linear;
        font-size: 14px;
        margin-right: 30px;
        margin-right: clamp(15px, calc(2vw + 2px), 30px);

        &:hover {
          filter: brightness(90%);
        }
      }

      .signup {
        font-weight: bold;
        color: $grey-text;
        transition: 0.1s linear;
        font-size: 14px;

        &:hover {
          filter: brightness(60%);
        }
      }
    }
  }
}

.navbar-sticky {
  position: fixed;
  top: 0;
  width: 100%;
}

.navbar-shadow {
  box-shadow: $box-shadow-value;
}