.benefits {
  display: flex;
  margin-bottom: 40px;
  // gap: 10px;
  &-card {
    display: flex;
    padding: 20px 30px;
    background-color: $grey-bg;
    border-radius: 5px;
    margin: 0 10px 0 0;
    // // gap:10px;
    // @supports not (gap: 10px) {
    //   margin: 0px 10px;
    // }

    img {
      margin-right: 20px;
    }
  }

  &-content {
    &-title {
      font-size: clamp(10px, 2vw, 14px);
      color: #000;
      font-weight: bold;
      margin-bottom: 10px;
    }

    &-desc {
      font-size: clamp(10px, 2vw, 14px);
      color: $grey-text;
    }
  }
}