.star-votes {
  margin-bottom: 10px;
  margin-top: 10px;


  &-wrapper {
    display: flex;
    align-items: center;

    .votes-number {
      font-size: 14px;
      color: #000;
      margin-right: 6px;
      width: 10px;
    }

    .votes-star {
      margin-right: 10px;
    }

    .progress {
      &-bar {
        width: 100%;
        max-width: 200px;
        height: 8px;
        background-color: $grey-bg;
        border-radius: 12px;
        overflow: hidden;
        margin-right: 10px;
      }

      &-fill {
        height: 100%;
        // width: 75%;
        background-color: $blue-primary;
        border-radius: 12px;
      }
    }

    .votes-count {
      font-size: 14px;
      color: #000;
    }
  }
}