.voucher {
  padding: 10px;
  box-shadow: $box-shadow-value;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  border-radius: 5px;
  width: 100%;

  &>*+* {
    margin-top: 5px;
  }

  &-title {
    font-size: 14px;
  }

  &-preview {
    font-size: 14px;
    color: $grey-text;
  }

  &-buttons {
    font-size: 14px;
    display: flex;
    justify-content: space-between;
    color: $blue-primary;
    font-weight: bold;

    * {
      cursor: pointer;
    }
  }
}