.product-description-area {
  border-radius: 12px;
  box-shadow: $box-shadow-value;
  background: #FFFFFF 0% 0% no-repeat padding-box;
  overflow: hidden;
  opacity: 1;
  margin-bottom: 10px;

  .tab {
    display: flex;

    &-link {
      width: 220px;
      height: 50px;
      display: flex;
      justify-content: center;
      align-items: center;
      color: $grey-text;
      font-weight: bold;
      cursor: pointer;
      font-size: 14px;
      transition: 0.1s all linear;

      span {
        color: $blue-primary;
        font-weight: 500;
        margin-left: 3px;
      }

      &:hover {
        filter: $filter-grey-text;
      }
    }

    &-active {
      background-color: $blue-primary !important;
      color: #fff;
    }
  }

  .tab-detail {
    padding: 30px;
    padding-bottom: 20px;

    h3 {
      font-size: 18px;
      color: #000;
      margin-bottom: 10px;

      span {
        font-size: 14px;
        color: $grey-text;
        font-weight: 500;
      }
    }

    .product-description {
      margin-bottom: 25px;

      .desc {
        font-size: 14px;
        color: #000
      }
    }

    .product-review {
      margin-bottom: 25px;

      .feedback-reviewer-wrapper {

        .pagination-wrapper {
          display: flex;
          justify-content: center;
          margin-top: 20px;
        }

      }
    }

    .product-discussion {
      h3 {
        font-size: 18px;
        color: #000;
        margin-bottom: 10px;

        span {
          font-size: 14px;
          color: $grey-text;
          font-weight: 500;
        }

        a {
          font-size: 14px;
          color: $blue-primary;
          font-weight: bold;
        }
      }

      .product-name {
        color: $grey-text;
        font-size: 14px;
        font-weight: bold;
        margin-bottom: 10px;
      }
    }

    .isLoginInProduct {
      display: flex;
      justify-content: space-between;
      align-items: center;
      height: 72px;
      padding: 0 20px;
      background-color: $grey-bg;
      border-radius: 12px;
      
      .desc{
        color:"#000";
        font-size: 14px;
        font-weight: 500;

        span {
          font-size: 14px;
          color: $grey-text;
          font-weight: 500;
        }

        a {
          font-size: 14px;
          color: $blue-primary;
          font-weight: bold;
          cursor: pointer;
          transition: 0.1s all linear;

          &:hover{
            filter: $filter-grey-text;
          }
        }
      }

      .btn-ask{
        height: fit-content;
        padding: 8px 20px 7px 20px;
        border-radius: 5px;
        border: none;
        background-color: $grey-text;
        color: #fff;
        font-weight: bold;
        font-size: 14px;
        white-space: nowrap;
        transition: 0.1s all linear;

        &:hover{
          filter: $filter-grey-text;
        }
      }
    }
  }
}