@import './mixins';
@import './variables';

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Poppins', sans-serif;
}

a {
  text-decoration: none;
  color: #000;
}

ul,
li {
  list-style: none;
}

button:focus {
  outline: none;
}


.container {
  padding: 0 7%;
  margin: auto;
  max-width: $max-width;
  min-width: $min-width;
}

body {
  #root {
    overflow-x: hidden;
    @import './label';
    @import './voucher';
    @import './product-quality';
    @import './hero/hero';
    @import './hightlight/hightlight';
    @import './card/card';
    @import './flash-sale';
    @import './most-popular';
    @import './best-selling';
    @import './big-cart';
    @import './partners';
    @import './about/';
    @import './footer';
    @import './breadcrumb';
    @import './product-details/product-info-bottom';
    @import './product-detail-page.scss';
    @import './productDescription/';
    @import './detail-recommendation';
  }
}