.card {
  position: relative;
  display: flex;
  flex-direction: column;
  padding-bottom: 10px;
  border-radius: 8px;
  box-shadow: $box-shadow-value;
  opacity: 1;
  overflow: hidden;
  cursor: pointer;
  height: 100%;

  &-image {
    align-self: center;
    position: relative;
    width: 100%;
    max-width: 200px;

    img {
      display: block;
      width: 100%;
      height: auto;
    }
  }

  .tooltip {
    position: absolute;
    right: 0;
    top: 0;
    margin-top: 10px;
    margin-right: 10px;
    background-color: $red-accent;
    font-size: 12px;
    font-weight: bold;
    color: #fff;
    padding: 0 12px;
    border-radius: 5px;
  }

  &-body {
    padding: 10px;
    position: relative;

    h5 {
      font-size: 14px;
      // font-size: clamp(12px, calc(1vw + 1px), 14px);
      font-weight: 500;
      // white-space: nowrap;
      text-overflow: ellipsis;
      overflow: hidden;
      display: -webkit-box;
      -webkit-line-clamp: 2;
      -webkit-box-orient: vertical;
    }

    p {
      font-size: 14px;
      font-size: clamp(12px, calc(1vw + 1px), 14px);

      span {
        font-weight: bold;
      }
    }

    .card-description {
      position: relative;

      .discount {
        font-size: 12px;
        font-size: clamp(11px, calc(1vw), 12px);
        color: $grey-text;
        text-decoration: line-through;
      }

      .price {
        font-size: 14px;
        font-size: clamp(12px, calc(1vw + 1px), 14px);
        font-weight: bold;
        color: $red-accent;
      }
    }
  }
}
