.most-popular {
  position: relative;
  margin-bottom: 40px;

  .container {
    padding: 0 5%;
    min-width: unset;
  }

  &-head {
    position: relative;
    display: flex;
    flex-direction: row;
    padding: 0 12px 20px 12px;

    h3 {
      font-size: 20px;
      font-weight: bold;
      color: #000;
      display: inline-block;
    }

    p {
      position: absolute;
      font-size: 14px;
      font-weight: bold;
      color: $blue-primary;
      right: 12px;
      transition: 0.1s linear;
      cursor: pointer;

      &:hover {
        cursor: pointer;
        filter: $filter-grey-text;
      }
    }
  }

  .slick-slider {
    // padding: 0 50px;
    
    .slick-track {
      display: flex;
      flex-wrap: wrap;
    }

    .slick-slide {
      display: flex;
      flex-direction: row;
      height: inherit;
      // max-width: 200px !important;
      // width: fit-content !important;
      padding: 5px 16px;
      // right: -40px;
      position: relative;

      // &>div {
      //   max-width: 200px;
      // }
    }

    .slick-next::before,
    .slick-prev::before {
      display: inline-block;
      color: blue;
      font-size: 50px;
      width: 50px;
      height: 50px;
      opacity: 1;
      border-radius: 50%;
      transform: scale(1.4);
      pointer-events: none;
    }
  }

  .slick-prev {
    // margin-top: -40px;
    // margin-left: -2%;
    // position: relative;
    left: -70px;
    width: 50px;
    height: 50px;
    z-index: $z-index-slick-next-prev;
    border-radius: 50%;

    &::before {
      content: url("../assets/slider-icons/icon-prev.svg");
    }
  }

  .slick-next {
    // margin-top: -40px;
    right: -70px;
    width: 50px;
    height: 50px;
    z-index: $z-index-slick-next-prev;
    border-radius: 50%;

    &::before {
      content: url("../assets/slider-icons/icon-next.svg");
      position: relative;
      // left: 50px;
    }
  }
}
