.menu-slider {
  // overflow: hidden;
  position: absolute;
  width: 100%;

  a {
    padding: 5px 20px;
    background-color: $grey-bg;
    border-radius: 12px;
    color: $grey-text;
    font-size: clamp(10px, 2vw, 14px);
    font-weight: bold;

    &:focus {
      outline: unset;
    }
  }

  .slick-slider {

    .slick-list {

      .slick-track {
        width: max-content !important;
        display: flex;
        
        .slick-slide {
          margin-right: 10px;

          &:hover {

            a {
              transition: all 0.1s linear;
              // filter: $filter-grey-text;
              background-color: #A8A8A8;
              color: #fff;
            }

            a.active {
              // filter: unset;
              background-color: $blue-primary;
              color: #fff;
            }
          }

          .active {
            background-color: $blue-primary;
            color: #fff;
          }
        }
      }
    }

    .slick-next {
      width: 35px;
      height: 35px;
      position: absolute;
      border-radius: 50%;
      transform: scale(1) translate(-18%, -46%);

      &::before {
        content: "";
        background-image: url('../../assets/highlight-icon/arrow-next.svg');
        background-size: cover;
        display: inline-block;
        width: 35px;
        height: 35px;
        font-size: 35px;
        opacity: 1;
        border-radius: 50%;
        background-size: 35px 35px;
        transform: scale(1.5) translateX(2px);
        pointer-events: none;
      }
    }

    .slick-prev {
      position: absolute;
      z-index: $z-index-slick-next-prev;
      transform: scale(1) translate(18%, -52%);
      height: 35px;
      width: 35px;

      &::before {
        background-image: url('../../assets/highlight-icon/arrow-next.svg');
        opacity: 1;
        content: "";
        display: block;
        height: 35px;
        width: 35px;
        background-size: 35px 35px;
        background-repeat: no-repeat;
        transform: scale(1.5) rotate(180deg);
        pointer-events: none;
      }
    }
  }
}