.feedback-reviewer {
  margin-top: 20px;

  .reviewer {
    display: flex;
    margin-bottom: 10px;

    &-img {
      width: 40px;
      height: 40px;
      border-radius: 20px;
      margin-right: 20px;
    }

    &-data {
      margin-right: 20px;
    }

    &-name {
      font-size: 14px;
      font-weight: bold;
      color: #000;

      span {
        font-size: 14px;
        font-weight: 500;
        color: $grey-text;
      }
    }

    &-date {
      font-size: 14px;
      color: $grey-text;
    }

    .option {
      display: flex;
      flex: 1;
      justify-content: flex-end;
      align-items: center;
      transition: 0.1s all linear;
      position: relative;

      &-menus {
        position: absolute;
        background-color: #fff;
        transform: translateY(60%);
        border-radius: 3px;
        box-shadow: $box-shadow-value;
        width: 12rem;
        padding: 12px;
        right: 0;

        div {
          transition: 0.1s all linear;
          font-size: 14px;
          cursor: pointer;

          &:hover {
            background-color: $grey-bg;
          }
        }

        &-overlay{
          position: fixed;
          width: 100vw;
          height: 100vh;
          top:0;
          right: 0;
          background-color: transparent;
          z-index: 999;
        }
      }
    }
  }

  .feedback {
    width: 100%;

    &-value {
      font-size: 14px;
      color: #000;
    }

    form {
      display: flex;
      height: 40px;

      .form-value {
        width: 100%;
        margin-right: 20px;
        background-color: $grey-bg;
        border: none;
        border-radius: 12px;
        padding: 10px 20px;

        &:focus {
          outline: none;
        }
      }

      .submit {
        border: none;
        background-color: white;
        padding: 0 5px;
        border-radius: 5px;
        color: $grey-text;
        font-size: 14px;
        font-weight: bold;
        cursor: no-drop;

        &:focus {
          outline: none;
        }
      }
    }
  }

  .reply {
    margin-top: 10px;
    margin-left: 40px;
    margin-bottom: 30px;

    &-action {
      display: flex;

      div {
        margin-right: 10px;
        font-size: 14px;
        font-weight: bold;
        color: $blue-primary;
        cursor: pointer;
        transition: 0.1s all linear;

        &:hover {
          filter: $filter-grey-text;
        }
      }

      img {
        cursor: pointer;
        transition: 0.1s all linear;

        &:hover {
          filter: $filter-grey-text;
        }
      }
    }
  }
}