.banner {
  position: relative;
  height: 300px;
  margin-bottom: 30px;

  .banner-slider {
    height: 300px;
    border-radius: 12px;
    -webkit-mask-image: -webkit-radial-gradient(white, black);
    // border-radius: 5px 5px 10px 10px;

    overflow: hidden;
    box-shadow: 2px 1px 10px #00000029;
    opacity: 1;
  }

  .img-slider {
    max-width: 100%;
    height: 300px;
  }

  .slick-dots {
    transform: translateY(-200%);
  }

  .slick-dots li {
    transform: scale(0.6);
    margin: 0 2px;
  }

  .slick-dots .slick-active {
    transform: scale(1);
  }

  .slick-dots li button:hover:before,
  .slick-dots li button:focus:before {
    opacity: 0.5;
  }

  .slick-dots li button:before {
    font-size: 15px;
    color: #fff;
    opacity: 1;
  }

  .slick-dots li.slick-active button:before {
    color: #026bb9;
    opacity: 1;
  }

  .slick-prev {
    position: absolute;
    left: 40px;
    z-index: $z-index-slick-next-prev;
    height: 35px;

    &::before {
      // font-size: 50px;
      opacity: 1;
      content: "";
      display: block;
      height: 35px;
      width: 35px;
      background-size: 35px 35px;
      background-image: url('../../assets/arrow.svg');
      background-repeat: no-repeat;
      transform: scale(1.7);
    }
  }

  .slick-next {
    position: absolute;
    right: 60px;
    z-index: $z-index-slick-next-prev;
    height: 35px;

    &::before {
      // font-size: 50px;
      opacity: 1;
      content: "";
      display: block;
      height: 35px;
      width: 35px;
      background-size: 35px 35px;
      background-image: url('../../assets/arrow.svg');
      background-repeat: no-repeat;
      transform: scale(1.7) rotate(180deg);
    }
  }

  .slick-slide:focus {
    outline: none;
  }
}